<template>
  <div id="home">
    <el-row>
      <el-col :span="15">
        <div class="home-header">
          <el-card shadow="always" class="el-page">
            <el-row>
              <el-col :span="3">
                <span style="font-size: 23px;color: #1abc9c"><b></b></span>
              </el-col>
              <el-col :offset="19" :span="2">&nbsp;<span v-text="total"></span> </el-col>
            </el-row>
          </el-card>
        </div>
        <div class="home-main">
          <el-row class="card-main-buttom" v-for="(user,index) in users" :key="users.id">
            <el-col :span="9" class="el-page">
              <el-card shadow="always">
<!--                <h3 class="el-card__header" @click="getBlogArticle(user.blogId)" style="color: #1abc9c" v-text="user.blogTitle"></h3>-->
                <el-link :underline="false" @click="getBlogArticle(user.blogId)" style="font-size: 18px"><h3 class="el-card__header" style="color: #1abc9c" v-text="user.blogTitle"></h3></el-link>
                <div id="content">
                  <p style="color: #999999" id="content-text" class="el-card__body"
                     v-text="user.blogContent.substring(0,100)">
                  </p>

                  <div id="content-img">
                    <img :src="user.blogPhoto" class="el-image" width="150px"/>
                  </div>
                  <div id="content-footer" class="el-card__body">
                    <div id="content-footer-data" style="font-size: 0.8em">

                      <i class="el-icon-user"></i><span v-text="user.user.userName"></span>
                      <i class="el-icon-date" style="margin-left: 30px"></i><span v-text="user.blogDate"/>
                      <a href="#" class="el-tag" style="margin-left: 140px" v-text="user.sort.sortName"></a>

                    </div>

                  </div>
                </div>
              </el-card>
            </el-col>


          </el-row>
        </div>

        <el-pagination class="paging"
                       background
                       @current-change="handleCurrentChange"
                       :page-size="5"
                       layout="prev, pager, next, jumper"
                       :total="total">
        </el-pagination>
      </el-col>

      <el-col :offset="1" :span="7">
        <div class="main-left">
          <el-card class="box-card card-left-top">
            <div slot="header" class="clearfix">
              <span></span>
              <el-button style="float: right; padding: 3px 0" type="text">more</el-button>
            </div>
            <div v-for="(newBlog,i) in newBlogs" :key="newBlog.id" class="text item">
              <el-row :gutter="12" style="padding-top: 0px">
                <el-col :span="24">
                  <el-card shadow="hover" style="height: 50px">
                    <el-link :underline="false" v-text="newBlog.blogTitle" @click="getBlogArticle(newBlog.blogId)"
                             style="margin-left: 15px;margin-top: -12px" class="text item">
                    </el-link>
                  </el-card>
