                <el-button style="float: right; padding: 3px 0" type="text">more</el-button>
              </router-link>

            </div>
            <div v-for="(sort,index) in sorts" :key="sort.id" class="text item">
              <div>
                <el-card shadow="always">
                  <el-link :underline="false" @click="getBlogBySort(sort.sortId)">
                    <div size="50px" v-text="sort.sortName"></div>
                  </el-link>
                </el-card>
              </div>


            </div>
          </el-card>

        </div>
      </el-col>



    </el-row>



  </div>
</template>

<script>
  import img from '../../assets/img/logo.png'
  import {blog} from "../../mixins/blog";

  export default {
    name: "MainPageOne",
    data() {
      return {
        sorts: [],
        users: [],
        newBlogs: [],
        //  
        total: null,
      }
    },

    methods: {
      //
      // getBlogData() {
      //   const _this = this
      //   this.$axios.get('http://localhost:8282/fxfblog/blog/queryAllBlog').then(function (response) {
      //     _this.users = response.data
      //     console.log(response.data)
      //   })
      // },
      //
      getNewBlog() {
        const _this = this
        this.$axios.get('http://localhost:8282/fxfblog/blog/findNewBlog').then(function (response) {
          _this.newBlogs = response.data
          console.log(response.data)
        })
      },
      getBlogByBlogTitle(){
        console.log(this.blogTitle);
        const _this = this
        this.$axios
          .get("http://localhost:8282/fxfblog/blog/findBlogByTitle/"+this.$route.query.blogTitle)
          .then(function (response) {
            console.log(response.data)
          })
      },
      getTableData() {
        //
        const _this = this
        this.$axios
          .get("http://localhost:8282/fxfblog/blog/queryBlogByPage/1")
          .then(function (response) {
            _this.users = response.data.list
            _this.total = response.data.total
            console.log(response.data.list)
          })
      },
      //
      handleCurrentChange(val) {
        const _this = this
        this.$axios
          .get("http://localhost:8282/fxfblog/blog/findBlogByPage/" + val)
          .then(function (response) {
            _this.users = response.data.list
            _this.total = response.data.total
            console.log(response.data.list)
          })
        console.log(`: ${val}`);
      },
      getBlogBySort(sortId) {
        const {href} = this.$router.resolve({
          path: '/home/ClassifyPage',
          query: {
            sortId: sortId
          }
        })
        window.open(href, '_blank')
      },
      getBlogArticle(blogId) {
        const {href} = this.$router.resolve({
          path: '/home/blogArticle',
          query: {
            blogId: blogId
          }

        })
        window.open(href, '_blank')
      }
    },
    created() {
      if (this.$route.query.blogTitle!=null){
        this.getBlogByBlogTitle()
      }else if(this.$route.query.blogTitle==null){
        this.getTableData()
      }

      this.getNewBlog()
      // this.getBlogData()
      //  
      const _this = this
      this.$axios.get('http://localhost:8282/fxfblog/sort/allSort').then(function (response) {
        _this.sorts = response.data
        console.log(response.data)
      })
    }

  }


</script>

