export const blog= {
    data(){
        return{
          tableData:null,
          blogTableData: null,
        }
    },
    methods:{
      getTableData(){
        //
        const _this = this
        this.$axios
          .get("http://localhost:8282/fxfblog/sort/findByPage/1")
          .then(function (response) {
            _this.ruleForm = response.data
            _this.tableData = response.data.list
            _this.total = response.data.total
            console.log(response.data.list)
          })
      },
      getBlogTableData() {

        const _this = this
        //
        this.$axios.get('/user/blog/findBlogByPage/1').then(function (response) {

          _this.users = response.data.list
          _this.blogTableData = response.data.list
          _this.total = response.data.total
          console.log(response.data)

        })
      }
    }
}
