<template>
  <div>
    <el-row :gutter="12" class="loginUser">
      <el-col :span="8">
        <el-card shadow="always" style="width: 450px">
          <h2 style="text-align: center;color: coral"></h2>
          <div style="margin: 20px;"></div>

          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="" prop="userName">
              <el-input placeholder="" v-model="ruleForm.userName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="userPassword">
              <el-input placeholder="" v-model="ruleForm.userPassword" show-password></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
              <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
    </el-row>

  </div>
</template>

<script>
    export default {
      name: "Login",
      data() {
        return {
          ruleForm: {
            useName: '',
            userPassword: ''
          },
          rules: {
            userName: [
              { required: true, message: '', trigger: 'blur' },
              { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
            ],
            userPassword: [
              { required: true, message: '', trigger: 'change' }
            ],

          }
        };
      },
      methods: {
        submitForm(formName) {
          const _this = this
          this.$refs[formName].validate((valid) => {
            if (valid) {
              _this.$axios.post("http://localhost:8282/fxfblog/user/login",_this.ruleForm).then(function (response) {
                console.log(response.data)
                if (response.data!=null){
                  _this.$alert(_this.ruleForm.userName+'','', {
                    confirmButtonText: '',
                    callback: action => {
                      _this.$router.push('/admin')
                    }
                  })
                  sessionStorage.setItem("userId",response.data.userId);
                  sessionStorage.setItem("userName",response.data.userName);
                  sessionStorage.setItem("photo",response.data.photo);
                  sessionStorage.setItem("token",response.data.token);
                  console.log(sessionStorage.getItem("token"))
                }else if (response.data==null){
                  _this.$alert('','', {
                    confirmButtonText: '',
                    callback: action => {
                      _this.$router.push('login')
                    }
                })
                }
              })

            } else {
              console.log('error submit!!');
              return false;
            }
          });
        },
        resetForm(formName) {
          this.$refs[formName].resetFields();
        }
      }

    }
</script>

<style scoped>
  .loginUser{
    position: center;
    left: 500px;
    top: 180px;

  }

</style>
