<template>
  <div class="ClassifyPage" style="height: 100%">
    <div class="card-top">
<!--      -->
      <el-card class="box-card">
        <span  v-for="(sort,index) in sorts" :key="sort.id" style="margin-left: 25px">
          <a @click="getBlogBySort(sort.sortId)"><el-tag v-text="sort.sortName"></el-tag></a>
        </span>


      </el-card>
    </div>
<!--    -->
    <div class="card-content">
      <el-row class="card-main-buttom" v-for="(user,index) in users" :key="users.id">
        <el-col :span="9" class="el-page">
          <el-card shadow="always">
            <h3 class="el-card__header" @click="getBlogArticle(user.blogId)" v-text="user.blogTitle"></h3>
            <div id="content">
              <p style="color: #999999" id="content-text" class="el-card__body" v-text="user.blogContent.substring(0,200)">

              </p>

              <div id="content-img">
                <img :src="user.blogPhoto" class="el-image" width="150px"/>
              </div>
              <div id="content-footer" class="el-card__body">
                <div id="content-footer-data" style="font-size: 0.8em">
                  <i class="el-icon-user"></i>&nbsp;<span v-text="user.user.userName"></span>
                  <i class="el-icon-date" style="margin-left: 30px"></i><span v-text="user.blogDate"></span>
                  <a href="#" class="el-tag" style="float: right" v-text="user.sort.sortName"></a>
                </div>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>

    <div class="paging">
      <el-pagination class="paging"
                     background
                     @current-change="handleCurrentChange"
                     :page-size="5"
                     layout="prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </div>

  </div>
</template>

<script>
    import img from "../../assets/img/logo.png";

    export default {
        name: "ClassifyPage",
        data(){
          return{
            users:[

            ],
            sorts:[

            ],
            //  
            total: null,
          }

        },
      methods:{
        handleCurrentChange(val) {
          if (this.$route.query.sortId==null){
            const _this = this
            this.$axios
              .get("http://localhost:8282/fxfblog/blog/queryBlogByPage/" + val)
              .then(function (response) {
                _this.users = response.data.list
                _this.total = response.data.total
                console.log(response.data.list)
              })
            console.log(`: ${val}`);
          }
          },

        //
        getAllBlogSort(){
          const _this = this
          this.$axios.get('http://localhost:8282/fxfblog/sort/allSort').then(function (response) {
            _this.sorts = response.data
            console.log(response.data)
          })
        },
        //
        getBlogByPage() {
          const _this = this
          this.$axios.get('http://localhost:8282/fxfblog/blog/queryBlogByPage/1').then(function (response) {
            _this.users = response.data.list
            _this.total = response.data.total
            console.log(response.data)
          })
        },
        getBlogBySort(sortId) {
          this.$router.push({
            path: '/home/ClassifyPage',
            query: {
              sortId: sortId
            }
          })
        },
        //id
        getBlogArticle(blogId) {
          const {href} = this.$router.resolve({
            path: '/home/blogArticle',
            query: {
              blogId: blogId
            }
          })
          window.open(href, '_blank')
        },
        //sortId
        getBlogBySortId(){
          const _this=this
          this.$axios.get('http://localhost:8282/fxfblog/blog/findBlogBySort/'+this.$route.query.sortId).then(function (response) {
            _this.users = response.data
            _this.total = ''
            console.log(response.data)
          })
        }

      },
      //
      watch:{
        '$route'(to, from){
          this.getBlogBySortId();
        }
      },

      created(){
        //  
        this.getAllBlogSort()
        // this.getBlogByPage()
        //sortIdsortId
        if (this.$route.query.sortId!=null){
          this.getBlogBySortId();
        }else if (this.$route.query.sortId==null){
          this.getBlogByPage()
        }

        // const _this = this
        // if (this.$route.query){
        //   this.sortId = this.$route.query.sortId;
        //   this.getBlogBySortId();
        //   this.init();
        //
        // }else if(this.$route.query.sortId==null){
        //   this.getBlogByPage()
        // }
      }
    }
</script>

<style scoped>
  .ClassifyPage{
    height: 900px;
    width: 1080px;
    padding-left: 200px;
  }
  el-tag{
    padding-left: 30px;
  }
  .card-content{
    width: 100%;
  }
  #content-text{
    width: 800px;
    float: left;
  }
  .el-page{
    width: 100%;
  }

  .paging{
    padding-top: 30px;
    text-align: center;
    line-height: 30px;
  }

</style>
