<template>
  <div id="all">
    <AdminNav></AdminNav>
    <div>
      <el-row style="padding-top: 35px">

        <el-col :offset="4" :span="16">
          <el-card shadow="always">
            <h1 style="text-align: center;color: #1abc9c;"></h1>
            <el-table :data="tableData" style="width: 100%;padding-top: 15px">

              <el-table-column label="" prop="sortId">
              </el-table-column>
              <el-table-column label="" prop="sortName">
              </el-table-column>
              <el-table-column
                align="right">
                <template slot="header" slot-scope="scope">
<!--                  -->
                  <el-button type="primary" @click="dialogFormVisible = true"></el-button>
<!--                  -->
                  <el-dialog title="" :visible.sync="dialogFormVisible" style="text-align: center">
                  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="" prop="sortName">
                      <el-input v-model="ruleForm.sortName"></el-input>
                    </el-form-item>
                  </el-form>
                    <div slot="footer" class="dialog-footer">
                      <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
                      <el-button @click="dialogFormVisible = false,closeForm()"> </el-button>
                    </div>
                  </el-dialog>

                </template>
                <template slot-scope="scope">
<!--                  -->
                  <el-button size="mini" @click="dialogUpdateVisible = true,getSortById(scope.row)"></el-button>
<!--                  -->
                  <el-dialog title="" :visible.sync="dialogUpdateVisible" style="text-align: center">
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                      <el-form-item label="" prop="sortName">
                        <el-input v-model="ruleForm.sortName"></el-input>
                      </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                      <el-button type="primary" @click="updateForm('ruleForm')"> </el-button>
                      <el-button @click="dialogUpdateVisible = false,closeForm()"> </el-button>
                    </div>
                  </el-dialog>

                  <el-popconfirm
                    confirm-button-text=''
                    cancel-button-text=''
                    icon="el-icon-info"
                    icon-color="red"
                    title=""
                    @confirm="handleDelete(scope.row)">
                    <el-button slot="reference" type="danger" size="mini"></el-button>
                  </el-popconfirm>
                </template>
              </el-table-column>
            </el-table>
            <div class="block" style="padding-top: 15px;line-height: 50px">
              <el-pagination
                :page-size="5"
                layout="prev, pager, next, jumper"
                :total="total"
                @current-change="page">
              </el-pagination>
            </div>
          </el-card>

        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
    import { blog } from '../../mixins/blog'
    import AdminNav from "./AdminNav";
    export default {
        name: "Type",
      components: {AdminNav},
      mixins:[blog],
      data(){
        return{
          //
          total:null,

          activeIndex: 'home',
          input:'',
          //
          userName:sessionStorage.getItem("userName"),
          circleUrl: sessionStorage.getItem("photo"),

          //
          dialogFormVisible: false,
          dialogVisible: false,
          dialogUpdateVisible:false,
          //
          ruleForm: {
            sortId:'',
            sortName: '',
          },
          rules: {
            sortName: [
              { required: true, message: '', trigger: 'blur'},
            ]
          },

          formLabelWidth: '120px'
          //

        }
      },

      created() {
        //  tokenuserIdlogin
        console.log(sessionStorage.getItem("token"))
        if (sessionStorage.getItem("userId")==null){
          this.$router.push('login')
        }
       this.getTableData()
      },

      methods: {
        //
        // getTableData(){
        //   //
        //   const _this = this
        //   this.$axios
        //     .get("http://localhost:8282/fxfblog/sort/findByPage/1")
        //     .then(function (response) {
        //       _this.ruleForm = response.data
        //       _this.tableData = response.data.list
        //       _this.total = response.data.total
        //       console.log(response.data.list)
        //     })
        // },

        //Id
        getSortById(row){
          const _this = this
          this.$axios.get('http://localhost:8282/fxfblog/sort/getSortById/'+row.sortId).then(function (response) {
            _this.ruleForm = response.data
          })
        },
        //  
        handleSelect(key, keyPath) {
          console.log(key, keyPath);
          this.$router.push(key).catch(err => err);
        },
        //
        submitForm(formName) {
          const _this = this
          console.log(this.$message)
          this.$refs[formName].validate((valid) => {
            if (valid) {
              _this.$axios.post('http://localhost:8282/fxfblog/user/sort/insertSort',this.ruleForm).then(function (response) {
                if(response.data=='success'){
                  //
                  _this.getTableData()
                  _this.$message.success("")
                  _this.ruleForm = {}
                  _this.dialogFormVisible=false
                }else if(response.data='error'){
                  _this.$message.error('');

                }
              })
            }else {
              return false;
            }
          });
        },
        //
        updateForm(formName){
          const _this = this
          this.$refs[formName].validate((valid) => {
            if (valid) {
              _this.$axios.put('http://localhost:8282/fxfblog/user/sort/updateSort',this.ruleForm).then(function (response) {
                if(response.data=='success'){
                  //
                  _this.getTableData()
                  //
                  _this.$message.success("")
                  _this.dialogUpdateVisible=false
                }else if(response.data='error'){
                  _this.$message.error('');
                }
              })
            }else {
              return false;
            }
          });

        },

        //
        handleDelete(row) {
          console.log(this)
          const _this = this
          this.$axios.delete("http://localhost:8282/fxfblog/user/sort/deleteSort/"+row.sortId).then(function (response) {
            _this.getTableData()
          })
          console.log(row.sortId);
        },

        //
        page(currentPage){
          const _this = this
          this.$axios
            .get("http://localhost:8282/fxfblog/sort/findByPage/"+currentPage)
            .then(function (response) {
              _this.tableData = response.data.list
              _this.total = response.data.total
              console.log(response.data.list)
            })
        },
        closeForm(){
          this.ruleForm = {}
        }


      },


    }
</script>

<style scoped>
  #all{
    width: 100%;
    height: 100%;
  }
