<template>
    <div id="all">
      <AdminNav></AdminNav>


      <div style="width: 800px;padding-left: 350px;padding-top: 30px">
<!--  -->
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
        <el-form-item prop="blogTitle">
          <el-input placeholder="..." v-model="ruleForm.blogTitle">
            <template slot="prepend"></template>
          </el-input>
        </el-form-item>
<!--     markdown-->
        <el-form-item prop="editorContent">
          <div id="main" style="padding-top: 20px">
            <mavon-editor
              v-model="ruleForm.blogContent"
              ref="md"
              @change="change"
              :ishljs = "true"
              @imgAdd="editorUpdataImgs"
              style="height: 500px"/>
          </div>
        </el-form-item>

<!--        -->
        <el-form-item prop="blogType">
          <el-select v-model="ruleForm.sortId" placeholder=""  style="padding-left: 15px">
            <el-option
              v-for="item in options"
              :key="item.sortId"
              :label="item.sortName"
              :value="item.sortId">
            </el-option>
          </el-select>
<!--    -->
        </el-form-item>
        <el-form-item prop="blogDate" label="">
          <el-date-picker
            v-model="ruleForm.blogDate"
            type="date"
            placeholder=""
            >
          </el-date-picker>
        </el-form-item>
<!--    -->
        <el-form-item prop="blogPhoto" label="">
<!--          <el-input placeholder="..." v-model="ruleForm.blogPhoto">-->
<!--            <template slot="prepend" ></template>-->
<!--          </el-input>-->
          <el-upload
            class="avatar-uploader"
            :headers="header"
            action="http://localhost:8282/fxfblog/user/blog/uploadfile/"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="ruleForm.blogPhoto" :src="ruleForm.blogPhoto" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon el-upload"></i>
          </el-upload>
        </el-form-item>
<!--    -->
        <el-form-item style="text-align: center">
          <el-button type="warning" plain></el-button>
          <el-button type="info" plain></el-button>
          <el-button type="success" @click="submit('ruleForm')" plain></el-button>
        </el-form-item>

      </el-form>
      </div>



    </div>
</template>

<script>
    import AdminNav from "./AdminNav";
    export default {
      name: "AddBlog",
      components: {AdminNav},
      data() {

        return {
          header:{
            token:sessionStorage.getItem("token"),
          },

          activeIndex: 'home',
          input: '',
          textarea: '',
          //
          userName: sessionStorage.getItem("userName"),
          //
          circleUrl: sessionStorage.getItem("photo"),

          ruleForm: {
            userId:sessionStorage.getItem("userId"),
            //
            blogTitle: '',
            //markdown
            blogContent: '',
            //
            blogDate: '',
            //
            blogPhoto: '',
          },
          //
          blogType: '',
          options: [{
            sortId: '',
            sortName: ''
          }],

          rules: {
            blogTitle: [
              {required: true, message: '', trigger: 'blur'},
              {min: 3, max: 20, message: ' 3  20 ', trigger: 'blur'}
            ],
            blogContent: [
              {required: true, message: '', trigger: 'blur'},
            ],

            blogDate: [
              {required: true, message: '', trigger: 'change'},
            ],
            blogPhoto: [
              {required: true, message: '', trigger: 'change'},
            ]
          },

          // //markdown
          // value: '',
          // value1:''
        }
      },
      created() {
        //  tokenuserIdlogin

        console.log(sessionStorage.getItem("token"));
        if (sessionStorage.getItem("userId") == null) {
          this.$router.push('login')
        }
        //  
        const _this = this;
        this.$axios
          .get("http://localhost:8282/fxfblog/sort/allSort")
          .then(function (response) {
            _this.options = response.data;
            console.log(response.data.list)
          })
        if (this.$route.query.blogId != null){
          this.$axios.get('http://localhost:8282/fxfblog/blog/findBlogById/'+this.$route.query.blogId).then(function (response) {
            console.log(response.data)
            _this.ruleForm = response.data
          })
        }
      },

      methods: {
        //

        handleSelect(key, keyPath) {
          console.log(key, keyPath);
          this.$router.push(key).catch(err => err);
        },
        editorUpdataImgs(filename, file) {
          //
          let formData = new FormData();
          console.log(formData)
          formData.append('file', file);
          var $vm = this
          this.$axios.post('http://localhost:8282/fxfblog/user/blog/uploadfile/', formData).then(function (response) {
            // if (response.body.message) {
            //   this.$refs.editor.$img2Url(filename, response.body.data);//
            // } else {
            //
            // }

              // console.log(response.data.data.url)
            $vm.$refs.md.$img2Url(filename, response.data.data.url);//

            console.log(response.data)
            console.log(response.data.data.url)
          }, function (response) {
            this.$message.error('!!');
          });
        },
        change(value, render) {        //valuemarkdownrenderhtml
          this.html = render;
        },
        // 
        submit(formName) {                    //htmlmarkdown
          console.log(this.ruleForm)
          const _this = this;
          this.$refs[formName].validate((valid) => {
            if (valid && this.$route.query.blogId == null) {
              _this.$axios.post('http://localhost:8282/fxfblog/user/blog/insertBlog', this.ruleForm).then(function (response) {
                console.log(response.data)
                if (response.data == 'success') {
                  //
                  console.log(response.data);
                  _this.$message.success("")
                } else if (response.data = 'error') {
                  _this.$message.error('');
                } else if (response.data = 'null') {
                  _this.$message.error('')
                }
              })
            }else if(this.$route.query.blogId != null){
              _this.$axios.put('http://localhost:8282/fxfblog/user/blog/updateBlog', this.ruleForm).then(function (response) {
                console.log(response.data)
                if (response.data == 'success') {
                  //
                  console.log(response.data);
                  _this.$message.success("")
                } else if (response.data = 'error') {
                  _this.$message.error('');
                }
              })
            } else {
              console.log('error submit!!');
              return false;
            }
          });


        },
        handleAvatarSuccess(res, file) {
           console.log(res)
          // console.log(file)
          this.ruleForm.blogPhoto = res.data.url;
          console.log(this.ruleForm.blogPhoto)
        },
        beforeAvatarUpload(file) {//
          const isJPG = file.type === 'image/jpeg';
          const isLt2M = file.size / 1024 / 1024 < 2;
          // console.log(file)
          if (!isJPG) {
            this.$message.error(' JPG !');
          }
          if (!isLt2M) {
            this.$message.error(' 2MB!');
          }
          return isJPG && isLt2M;
        }
      }
    }

</script>

<style scoped>
  #all{
    width: 100%;
    height: 100%;
  }
  /**/
  .el-menu{
    padding-left: 100px;
  }
  .log{
    margin-right: 100px;
    font-size: 30px !important;
  }
  .el-button--text{
    float: right;
    margin-right: 150px;
  }
  .el-dropdown-link {
    cursor: pointer;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
