              <el-row :gutter="12" style="padding-top: 0px">
                <el-col :span="24">
                  <el-card shadow="always" style="height: 50px">
                    <el-link :underline="false" v-text="newBlog.blogTitle" @click="getBlogArticle(newBlog.blogId)"
                             style="margin-left: 15px;margin-top: -12px" class="text item">
