<script>
  import Footer from "../../components/Footer";

    var hljs = require('highlight.js') // https://highlightjs.org/

    // 
    var md = require('markdown-it')({
      highlight: function(str, lang) {
        if (lang && hljs.getLanguage(lang)) {
          try {
            return '<pre class="hljs"><code>' + hljs.highlight(lang, str, true).value + '</code></pre>'
          } catch (__) {}
        }

        return '<pre class="hljs"><code>' + md.utils.escapeHtml(str) + '</code></pre>'
      }
    })
    export default {

        name: "BlogArticle ",
      components: {Footer},
      data(){

        return{
          sorts: [],
          newBlogs: [],
          lists:[
            {
              title:'fxf'
            },
            {
              id:1,
              title:'fzj'
            }
          ],
          blog:[
            {
              blogPhoto:'',
              blogTitle:'',
              userName:'',
              blogDate:'',
              sortName:'',
              blogContent:''

            }],

        }

      },
      methods: {
        goBack() {
          this.$router.push({
            path: '/home',

          })
          console.log('go back');
        },
        //
        getNewBlog() {
          const _this = this
          this.$axios.get('http://localhost:8282/fxfblog/blog/findNewBlog').then(function (response) {
            _this.newBlogs = response.data
            console.log(response.data)
          })
        },
        //id
        getBlogById(){
          const _this=this
          this.$axios.get('http://localhost:8282/fxfblog/blog/findBlogById/'+this.$route.query.blogId).then(function (response) {
            _this.blog=response.data
            _this.blog.blogContent=md.render(response.data.blogContent)
            console.log(response.data)
          })
        },
        getBlogBySort(sortId) {
          this.$router.push({
            path: '/ClassifyPage',
            query: {
              sortId: sortId
            }
          })
        },
        getBlogArticle(blogId) {
          const {href} = this.$router.resolve({
            path: '/blogArticle',
            query: {
              blogId: blogId
            }

          })
          window.open(href, '_blank')
        }
      },
      created() {
        this.getBlogById();
        //
        this.getNewBlog();
        //  
        const _this = this
        this.$axios.get('http://localhost:8282/fxfblog/sort/allSort').then(function (response) {
          _this.sorts = response.data
          console.log(response.data)
        })
      }
    }
</script>

