<template>
  <div class="comment">
    <div class="comment-header">
      <el-card :body-style="{ padding: '0px' }">
        <img src="http://img01.1sucai.com/180303/335313-1P3031S60571.jpg" class="img" width="900px"/>
      </el-card>
    </div>
    <div>
      <el-card shadow="always">
        <el-row>
          <el-col :span="3">
            <span style="font-size: 25px;color: #1abc9c"><b></b></span>

          </el-col>
          <el-col :offset="19" :span="2">&nbsp;<span v-text="number"></span> </el-col>

<!--          <div class="boke" style="float: right">-->

<!--          </div>-->
        </el-row>

      </el-card>
    </div>
    <div class="comment-main">

          <el-card shadow="never">
            <div class="comment-main-text" v-for="(comment,index) in comments" :key="comments.id">
              <el-row>
                <el-col :span="1">
                  <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>

                </el-col>
                <el-col :offset="1" :span="5" class="comment-mian-one" style="padding-top: 10px">
                  <span v-text="comment.commentName" style="font-size: 14px;color: coral"></span>
                </el-col>
                <el-col :offset="10" :span="7" style="padding-top: 10px">
                  <p style="font-size: 10px;color: #999999;padding-left: 30px"><span v-text="comment.commentDate"></span></p>
                </el-col>

              </el-row>
              <div class="comment-main-content" style="text-indent: 2em;font-family: Arial;color: violet;font-size: 20px"v-text="comment.commentContent">
              </div>
              <el-divider/>

            </div>
          </el-card>
    </div>
    <div class="comment-buttom" style="padding-top: 30px">
      <el-card shadow="never">
        <el-form :label-position="labelPosition" :rules="rules" ref="form" :model="addcomments" label-width="80px">

          <el-form-item label="">
            <el-input type="textarea" v-model="addcomments.commentContent"></el-input>
          </el-form-item>
          <el-form-item label="" prop="commentName">
            <el-input v-model="addcomments.commentName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="commentEmail">
            <el-input v-model="addcomments.commentEmail" placeholder="email...">
            </el-input>
          </el-form-item>


          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form>
      </el-card>

    </div>
  </div>
</template>

<script>
    export default {
        name: "LabelPage",
      data(){
        return{
          labelPosition:'top',
          addcomments: {

            commentContent:'',
            commentName:'',
            commentEmail:'',

          },
          comments:[{
            commentContent:'',
            commentName:'',
            commentDate:'',
            commentEmail:'',
          }],
          rules:{
            commentName:[
              { required: true, message: '', trigger: 'blur' },
              { min: 2, max: 6, message: ' 2  6 ', trigger: 'blur' }
            ],
            commentEmail:[
              { required: true, message: '', trigger: 'blur' },
              { type: 'email', message: '', trigger: ['blur', 'change'] }
            ],
          },
          number:''
        }
      },
      methods: {

        getAllComment(){
          const _this = this
          _this.$axios.get('http://localhost:8282/fxfblog/comment/queryAllComment').then(function (response) {
            console.log(response.data)
            _this.comments = response.data
            _this.number = _this.comments.length
          })
        },
        onSubmit(formName) {
          // console.log('submit!');
          const _this = this
          _this.$axios.post('http://localhost:8282/fxfblog/comment/insertComment',this.addcomments).then(function (response) {
            console.log(response.data)
            if (response.data=='success'){
              _this.$message.success('');
              _this.addcomments = {}
              // _this.router.push('labelPage')
              _this.getAllComment()
            }else if(response.data=='error'){
              _this.$message.error("")
              _this.addcomments = {}
            }
          })

        }
      },
      created() {
        this.getAllComment();
      }
    }
</script>

<style scoped>
  .comment{

    width: 900px;
    padding-left: 300px;
  }
</style>
